import { ref } from 'vue'
import { defineStore } from 'pinia'
import  axios  from "axios";

export const useProductStore = defineStore('product', {

  state: () => ({
      product: fpe.product,
      data_sets: fpe.data_sets,
      openPanel: false,
      isUpdating: false,
      tax_enabled: fpe.tax_enabled,
      strings: fpe.strings,
      manage_stocks: fpe.manage_stocks,
      tags: [],
      panelWidth: localStorage.getItem('fpe_panel_width') || '400px',
      editor_config: fpe.editor_config,
  }),
  actions: {
    updateProduct(){
      
      this.isUpdating = true;
      const { ajax_url, security, product_id, edit_mode } = fpe;
      const that = this;
      
      axios.post(`${ajax_url}?action=fpe_product_update&fpe_nonce=${security}`, {
          product: this.product,
          product_id,
          edit_mode,
          action: 'fpe_product_update'
      })
      .then( function (response) {
        if(response.data.success){
          if(edit_mode === 'archive'){
            window.location.reload();
          }else{
            window.location.href = response.data.product_url;
          }
          that.isUpdating = false;
        }
      })
      .catch(function (error){
        console.log('Error updating product', error); 
      })
    },
    togglePanel(){
      this.product = fpe.product;
      this.openPanel = !this.openPanel;
    },
    updatePanelSize(event){
      this.panelWidth = event.width;
      localStorage.setItem('fpe_panel_width', event.width + 'px');
    },
    async getTags(queryString){
      const { ajax_url } = fpe;
      const that = this;
      await axios.post(`${ajax_url}?action=ajax-tag-search&tax=product_tag&q=${queryString}&number=20`, {
         
      })
      .then( function (response) {
        if(response.data.length){
          that.tags = response.data.split('\n');
        }
      })
      .catch(function (error){
        console.log(error);
      })
    },
    async getProducts(queryString){
      const { ajax_url } = fpe;
      const that = this;
      await axios.post(`${ajax_url}?action=fpe_search_products&q=${queryString}&current=${this.product.id}`, {
         
      })
      .then( function (response) {
        that.tags = response.data;
      })
      .catch(function (error){
        console.log(error);
      })
    },
    async getUpsellProductsByIds(ids){
      
      const { ajax_url, security } = fpe;
      const that = this;
      await axios.post(`${ajax_url}?action=fpe_get_products&fpe_nonce=${security}`, {
          ids
      }).then( function (response) {
        that.product.upsell_ids = response.data;
      }).catch(function (error){
        console.log(error);
      })
    },
    async getCrossSellProductsByIds(ids){
      
      const { ajax_url, security } = fpe;
      const that = this;
      await axios.post(`${ajax_url}?action=fpe_get_products&fpe_nonce=${security}`, {
          ids
      }).then( function (response) {
        that.product.cross_sell_ids = response.data;
      }).catch(function (error){
        console.log(error);
      })
    }, 
    async getGroupedProductsByIds(ids){
        
        const { ajax_url, security } = fpe;
        const that = this;
        await axios.post(`${ajax_url}?action=fpe_get_products&fpe_nonce=${security}`, {
            ids
        }).then( function (response) {
          that.product.grouped_products = response.data;
        }).catch(function (error){
          console.log(error);
        })
    },
    async getAttributeTermsById(attribute, ids){
      
      const { ajax_url, security } = fpe;
      const that = this;
      await axios.post(`${ajax_url}?action=fpe_get_attribute_terms&fpe_nonce=${security}`, {
          attribute,
          ids
      }).then( function (response) {
        that.tags = response.data;
      }).catch(function (error){
        console.log(error);
      })
    },
  }, 
});
