<script setup>
import { ref, onMounted, onUnmounted } from 'vue';
import VueResizable from 'vue-resizable'

import Form from '../../components/form.vue' 

import { useProductStore } from '../../stores/fpe'
import { storeToRefs } from 'pinia'


const store = useProductStore();
const { openPanel, strings, panelWidth, product, editor_config } = storeToRefs(store);


const handlers = ref([  'l']);

const hasAdminBar = document.getElementById('wpadminbar') ? true : false;

// adminBarHeight ref
const adminBarHeight = ref(hasAdminBar ? document.getElementById('wpadminbar').offsetHeight : 0);
const panelHeight = ref(window.innerHeight - adminBarHeight.value);

// updted panel height on window resize
window.addEventListener('resize', function(){
    adminBarHeight.value = hasAdminBar ? document.getElementById('wpadminbar').offsetHeight : 0;
    panelHeight.value = window.innerHeight - adminBarHeight.value;
})

onMounted(() => {
    
    const fpe_editors = document.querySelectorAll('.fpe-editor');
    fpe_editors.forEach(function(editor){
        const textarea = editor.querySelector('textarea');
        const editor_id = textarea.getAttribute('id');
        
        wp.editor.remove(editor_id);
        wp.editor.initialize(editor_id, {
            tinymce: {
                wpautop: true, 
                plugins : 'charmap colorpicker hr lists paste tabfocus textcolor fullscreen wordpress wpautoresize wpeditimage wpemoji wpgallery wplink wptextpattern',
                toolbar1: 'formatselect,bold,italic,bullist,numlist,blockquote,alignleft,aligncenter,alignright,link,wp_more,spellchecker,fullscreen,wp_adv,listbuttons',
                toolbar2: 'styleselect,strikethrough,hr,forecolor,pastetext,removeformat,charmap,outdent,indent,undo,redo,wp_help',
                textarea_rows : 20,
                init_instance_callback: function (editor) {
                  editor.on('Change', function (e) {
                    
                    if(e.target.id == 'fpe_description'){
                        product.value.description = editor.getContent();
                    }
                    
                    if(e.target.id == 'fpe_short_description'){
                        product.value.short_description = editor.getContent();
                    }
                  });
                }
            },
            quicktags: true,
            mediaButtons: true
        });
        
        // Trigger Add Media 
        const add_media = editor.querySelector('.insert-media');
        add_media.addEventListener('click', function(event){
            
            var elem = jQuery( event.currentTarget ),
                editor = elem.data('editor'),
                options = {
                  frame:    'post',
                  state:    'insert',
                  title:    wp.media.view.l10n.addMedia,
                  multiple: true
                };

              if ( elem.hasClass( 'gallery' ) ) {
                options.state = 'gallery';
                options.title = wp.media.view.l10n.createGalleryTitle;
              }

              wp.media.editor.open( editor, options );
        });

        // Toggle editor mode (Visual/Text)
        const editor_switchers = editor.querySelectorAll('.wp-switch-editor');
        editor_switchers.forEach(function(switcher){
            switcher.addEventListener('click', function(event){
                event.preventDefault();
                window.switchEditors.go(editor_id);
            });
        });
        
    });
      

    document.body.classList.add('fpe-panel-open');
})

</script>

<template>
    <vue-resizable 
        :active="handlers" 
        class="fpe-panel" 
        @resize:end="store.updatePanelSize"
        :height="panelHeight" :top="adminBarHeight" :width="panelWidth" :left="left" :right="right" style="position: fixed; z-index: 2000;cursor: unset;">
        <div class="resizable-content">
            <header class="fpe-panel-header">
                <span class="header-title">Update Product ({{ product.name }})</span>
                <button aria-label="Close this dialog" @click="store.togglePanel" type="button"><i class="el-icon el-drawer__close"><svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 1024 1024"><path fill="currentColor" d="M764.288 214.592 512 466.88 259.712 214.592a31.936 31.936 0 0 0-45.12 45.12L466.752 512 214.528 764.224a31.936 31.936 0 1 0 45.12 45.184L512 557.184l252.288 252.288a31.936 31.936 0 0 0 45.12-45.12L557.12 512.064l252.288-252.352a31.936 31.936 0 1 0-45.12-45.184z"></path></svg></i></button>
            </header> 
            <div class="panel-body">
                <Form />
            </div>
            <div class="panel-footer">
                <PanelFooter />
            </div>              
        </div>
    </vue-resizable>
    <div class="fpe-panel-overlay"></div>
</template>

<style>
    .resizable-content{
        height: 100%;
        display: flex;
        flex-direction: column;
    }
    .resizable-body{
        overflow-y: auto;
        flex:1;
    }
</style>